import request from '@/assets/request'

let method = {
    //登录
    login(param){
        return request('/auth/login', {
            mock: false,
            body: {
                method: 'POST',
                data: param
            }
        });
    },
    //学生管理 接口开始
    getStudentList(param) {
        return request('/student/pageList', {
            mock: false,
            body: {
                method: 'POST',
                data: param
            }
        });
    },
    selectAllStudents(param){
        return request('/student/selectAll', {
            mock: false,
            body: {
                method: 'POST',
                data: param
            }
        });
    },
    selectStudentByPk(id) {
        return request('/student/selectByPk/' + id, {
            mock: false,
            body: {
                method: 'POST'
            }
        });
    },
    addStudent(param) {
        return request('/student/create', {
            mock: false,
            body: {
                method: 'POST',
                data: param
            }
        });
    },
    updateStudent(param) {
        return request('/student/update', {
            mock: false,
            body: {
                method: 'POST',
                data: param
            }
        });
    },
    deleteStudent(studentId) {
        return request('/student/delete/' + studentId, {
            mock: false,
            body: {
                method: 'POST'
            }
        });
    },
    //学生管理接口结束
    //付费记录管理开始
    getPaymentRecordList(paymentRecord) {
        return request('/paymentRecord/pageList', {
            mock: false,
            body: {
                method: 'POST',
                data:paymentRecord
            }
        });
    },
    addPaymentRecord(paymentRecord) {
        return request('/paymentRecord/create', {
            mock: false,
            body: {
                method: 'POST',
                data: paymentRecord
            }
        });
    },
    updatePaymentRecord(paymentRecord) {
        return request('/paymentRecord/update', {
            mock: false,
            body: {
                method: 'POST',
                data: paymentRecord
            }
        });
    },
    selectPaymentRecordByPK(id) {
        return request('/paymentRecord/selectByPk/' + id, {
            mock: false,
            body: {
                method: 'POST'
            }
        });
    }
    //付费记录管理结束
};

export default method;
