<template>
    <div>
        <Tip :title="type" class="f-border-b"></Tip>
        <Form :model="formItem" label-position="right" :label-width="100" ref="formValidate" :rules="ruleValidate"
              style="width:50%;margin-left:20%;">

            <FormItem label="姓名" prop="studentName">
                <Input v-model="formItem.studentName" placeholder="姓名"></Input>
            </FormItem>
            <FormItem label="学生手机号" prop="studentPhone">
                <Input v-model="formItem.studentPhone" placeholder="学生手机号"></Input>
            </FormItem>

            <FormItem label="年级" prop="grade">
                <Input v-model="formItem.grade" placeholder="年级"/>
            </FormItem>
            <FormItem label="微信号" prop="weChartId">
                <Input v-model.number="formItem.weChartId"  placeholder="微信号"/>
            </FormItem>

            <FormItem label="家长姓名" prop="parentName">
                <Input v-model.number="formItem.parentName" placeholder="家长姓名"/>
            </FormItem>
            <FormItem label="家长手机号" prop="parentPhone">
                <Input v-model.number="formItem.parentPhone" placeholder="家长手机号"/>
            </FormItem>
            <FormItem label="课时费" prop="fee">
                <Input v-model="formItem.fee" placeholder="课时费">
                    <Icon type="logo-yen" slot="prefix" />
                </Input>
            </FormItem>
            <FormItem label="学习次数" prop="learnTimes">
                <Input v-model="formItem.learnTimes" placeholder="学习次数"/>
            </FormItem>
            <FormItem label="缴费次数" prop="paymentTimes">
                <Input v-model="formItem.paymentTimes" placeholder="缴费次数"/>
            </FormItem>
            <FormItem>
                <Button type="primary" @click="submit('formValidate')">提交</Button>
                <Button type="primary" @click="goBack" style="margin-left: 8px">返回</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
    import _apiitems from '@/api/items'
    import Tip from '@/components/Tip'

    export default {
        name: "students-view",
        props: {
            type: {
                type: String
            },
            title: {
                type: String
            },
            studentId: {
                type: String,
                default: ''
            },
        },
        components: {
            Tip,
        },
        data() {
            return {
                show: false,
                formItem: {
                    studentName: '',
                    studentPhone: '',
                    grade: '',
                    weChartId: '',
                    parentName: '',
                    parentPhone: '',
                    fee: 0,
                    learnTimes: 0,
                    paymentTimes: 0
                },
                ruleValidate: {
                    studentName: [
                        {required: true, message: "请输入学生姓名", trigger: 'blur'}
                    ],
                    fee: [
                        {required: true, message: "请输入课时费", trigger: 'blur'}
                    ],
                },
            }
        },
        computed: {},
        mounted() {
        },
        created() {
            if (this.title == 'add') {
                this.show = true
            } else if (this.title == 'update') {
                _apiitems.selectStudentByPk(this.studentId).then(res => {
                    this.formItem = res.data;
                    this.show = true
                })
            }
        },
        methods: {
            submit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        if (this.title == 'add') {
                            _apiitems.addStudent({...this.formItem}).then(res => {
                                if (res) {
                                    this.$Notice.success({title: '新增成功!'})
                                    this.$emit('childFn', 'list');
                                }
                            }).catch(err => {
                                this.$Notice.error({title: '新增失败!'})
                            })
                        } else {
                            _apiitems.updateStudent({...this.formItem}).then(res => {
                                if (res) {
                                    this.$Notice.success({title: '修改成功!'})
                                    this.$emit('childFn', 'list');
                                }
                            }).catch(err => {
                                this.$Notice.error({title: '修改失败!'})
                            })
                        }
                    } else {
                        this.$Notice.error({title: '有数据未填写!'})
                    }
                })
            },
            goBack() {
                this.$emit('childFn', 'list');
            },
        }
    }
</script>

<style scoped>

</style>
